.header {
  display: flex;
  justify-content: center;
  padding: 15px;
  margin-top: 50px;
}
.header .logo-box {
  margin-right: 25px;
}
.header .logo-box .logo {
  width: 50px;
  height: 50px;
}
.header .locations {
  display: flex;
}
.header .locations .location {
  cursor: pointer;
  background-color: transparent;
  color: #747474;
  font-size: 20px;
  padding: 5px 7.5px;
  margin: auto 5px;
  border-radius: 3px;
  transition: all 0.2s ease-out;
}
.header .locations .location:hover {
  color: #ddd;
}
.header .locations .location.active {
  color: #202020;
  background-color: #ddd;
}
.login-form {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 500px;
  height: 300px;
  margin-top: -150px;
  margin-left: -250px;
  display: flex;
  flex-direction: column;
}
.login-form .form-title {
  text-align: center;
  margin-bottom: 15px;
  font-size: 40px;
  color: #ddd;
}
.login-form .form-token {
  margin-bottom: 15px;
  font-size: 40px;
  color: #ddd;
  user-select: text;
}
.login-form .form-field {
  width: 225px;
  margin: 10px auto;
  padding: 5px 7.5px;
  font-size: 17px;
  color: #ddd;
  background-color: #202020;
  border: 1px solid #ddd;
  border-radius: 5px;
}
.login-form .form-button {
  margin: 10px auto;
  padding: 5px 7.5px;
  font-size: 20px;
  color: #ddd;
  background-color: #202020;
  border: 1px solid #ddd;
  border-radius: 3px;
  cursor: pointer;
  transition: all 0.2s ease-out;
}
.login-form .form-button:hover {
  color: #202020;
  background-color: #ddd;
}
.login-form .error-box {
  color: #E62737;
  text-align: center;
  font-size: 20px;
  margin: 20px 0px 10px 0px;
}
.modal {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: none;
  background-color: rgba(0, 0, 0, 0.5);
}
.modal.active {
  display: block;
}
.modal .modal-body {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 500px;
  height: auto;
  border-radius: 3px;
  display: flex;
  flex-direction: column;
  background-color: #121212;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}
.modal .modal-body .modal-header {
  flex-grow: 0;
  display: flex;
  padding: 15px;
  border-top-right-radius: 3px;
  border-top-left-radius: 3px;
  background-color: #202020;
}
.modal .modal-body .modal-header .modal-title {
  flex-grow: 1;
  font-size: 25px;
  color: #ddd;
}
.modal .modal-body .modal-header .modal-close {
  flex-grow: 0;
  padding: 0px;
  cursor: pointer;
  color: #ddd;
  background-color: transparent;
}
.modal .modal-body .modal-header .modal-close img {
  width: 25px;
  height: 25x;
}
.modal .modal-body .modal-content {
  padding: 15px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}
.modal .modal-body .modal-content .upload-modal-content {
  display: flex;
  flex-direction: column;
  font-size: 20px;
  color: #ddd;
}
.modal .modal-body .modal-content .upload-modal-content .chosen {
  text-align: center;
}
.modal .modal-body .modal-content .upload-modal-content #files {
  margin: 10px auto;
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}
.modal .modal-body .modal-content .upload-modal-content #file-list {
  margin: 0px auto;
}
.modal .modal-body .modal-content .upload-modal-content #file-list li {
  margin: 10px;
  text-align: center;
}
.modal .modal-body .modal-content .upload-modal-content #file-list li a {
  cursor: pointer;
}
.modal .modal-body .modal-content .upload-modal-content #file-list li a:hover {
  border-bottom: 1px solid #ddd;
}
.modal .modal-body .modal-content .upload-modal-content #choose-btn {
  padding: 5px 7.5px;
  margin: auto;
  font-size: 17.5px;
  color: #ddd;
  background-color: #202020;
  border-radius: 3px;
  cursor: pointer;
}
.modal .modal-body .modal-footer {
  padding: 15px;
  flex-grow: 0;
  display: flex;
  justify-content: flex-end;
}
.modal .modal-body .modal-footer .modal-btn {
  padding: 5px 7.5px;
  margin: 0px 0px 0px 10px;
  font-size: 17.5px;
  color: #ddd;
  background-color: #202020;
  border-radius: 3px;
  cursor: pointer;
}
.permanent {
  margin: 3% auto 0px auto;
  max-width: 900px;
}
.permanent .explorer {
  display: flex;
  flex-direction: column;
  height: 600px;
  background-color: #202020;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  border-radius: 3px;
  font-weight: 400;
}
.permanent .explorer .list-header {
  display: flex;
  padding: 10px 0px;
  background-color: #222222;
}
.permanent .explorer .list-header .path {
  color: #ddd;
  flex-grow: 1;
  margin: auto 15px;
  text-align: start;
  font-size: 22px;
}
.permanent .explorer .list-header .control-buttons {
  flex-grow: 0;
  display: flex;
}
.permanent .explorer .list-header .control-buttons .control-button {
  display: block;
  background: transparent;
  margin: auto 5px;
  padding: 0px 7.5px;
  cursor: pointer;
}
.permanent .explorer .list-header .control-buttons .control-button img {
  filter: invert(0.7);
  transition: all 0.2s ease-out;
}
.permanent .explorer .list-header .control-buttons .control-button:last-child {
  margin-right: 5px;
}
.permanent .explorer .list-header .control-buttons .control-button.active img {
  filter: invert(0.5);
}
.permanent .explorer .list-header .control-buttons .control-button.active:hover img {
  filter: invert(0);
}
.permanent .explorer .main {
  display: flex;
  height: 100%;
  padding: 10px 0px;
  font-size: 20px;
  background-color: #121212;
}
.permanent .explorer .main .navbar {
  flex-grow: 0;
  width: 200px;
  padding: 0px 5px 0px 15px;
  border-right: 1px solid #747474;
  overflow-y: scroll;
  overflow-x: hidden;
}
.permanent .explorer .main .navbar .favourites {
  border-bottom: 1px solid #747474;
  margin-bottom: 5px;
  padding-bottom: 5px;
}
.permanent .explorer .main .navbar .favourites .favourites-title {
  padding: 5px 0px;
  color: #747474;
  cursor: pointer;
  transition: all 0.2s ease-out;
}
.permanent .explorer .main .navbar .favourites .favourites-title.active {
  color: #ddd;
}
.permanent .explorer .main .navbar .favourites .favourites-title:hover {
  color: #ddd;
}
.permanent .explorer .main .navbar .accordeon-title {
  margin: 5px 0px;
  color: #747474;
  transition: all 0.2s ease-out;
}
.permanent .explorer .main .navbar .accordeon-title.active {
  color: #ddd;
}
.permanent .explorer .main .navbar .accordeon-title:hover {
  color: #ddd;
}
.permanent .explorer .main .navbar .file-title {
  padding: 5px 0px;
  text-align: start;
  color: #747474;
  transition: all 0.2s ease-out;
  overflow-x: hidden;
  white-space: nowrap;
}
.permanent .explorer .main .navbar .file-title.active {
  color: #ddd;
}
.permanent .explorer .main .navbar .file-title:hover {
  color: #ddd;
}
.permanent .explorer .main .navbar .accordeon {
  display: flex;
  flex-direction: column;
  transition: all 0.1s ease-out;
}
.permanent .explorer .main .navbar .accordeon .accordeon-head {
  display: flex;
  cursor: pointer;
}
.permanent .explorer .main .navbar .accordeon .accordeon-head .dropdown-button {
  background-color: transparent;
  cursor: pointer;
}
.permanent .explorer .main .navbar .accordeon .accordeon-head .dropdown-button .dropdown-icon {
  width: 20px;
  height: 20px;
  transition: all 0.1s ease-out;
}
.permanent .explorer .main .navbar .accordeon .accordeon-head .dropdown-button .dropdown-icon.active {
  transform: rotate(180deg);
}
.permanent .explorer .main .navbar .accordeon .accordeon-body {
  display: none;
  transition: all 0.1s ease-out;
  transform: translateY(-10px);
}
.permanent .explorer .main .navbar .accordeon .accordeon-body.active {
  display: flex;
  flex-direction: column;
  visibility: visible;
  transform: translateY(0px);
}
.permanent .explorer .main .hierarchy {
  overflow-x: hidden;
  overflow-y: scroll;
  flex-grow: 1;
  padding: 0px 10px;
}
.permanent .explorer .main .hierarchy .current-folder-list {
  margin: 0px;
}
.permanent .explorer .main .hierarchy .current-folder-list .folder-list-el {
  display: flex;
  padding: 6px 15px;
  margin: 1px 0px 0px 0px;
  border-radius: 3px;
  font-size: 20px;
  transition: all 0.2s ease-out;
}
.permanent .explorer .main .hierarchy .current-folder-list .folder-list-el.active {
  background: #202020;
}
.permanent .explorer .main .hierarchy .current-folder-list .folder-list-el.active:hover {
  background: #292929;
}
.permanent .explorer .main .hierarchy .current-folder-list .folder-list-el:hover {
  background: #202020;
}
.permanent .explorer .main .hierarchy .current-folder-list .folder-list-el .el-icon {
  flex-grow: 0;
  margin: auto 25px auto 0px;
  width: 25px;
  height: 25px;
}
.permanent .explorer .main .hierarchy .current-folder-list .folder-list-el .el-name {
  flex-grow: 0;
  color: #747474;
  cursor: pointer;
  text-align: start;
}
.permanent .explorer .main .hierarchy .current-folder-list .folder-list-el .el-input {
  flex-grow: 0;
  background: transparent;
  font-size: 20px;
  color: #747474;
  cursor: pointer;
  text-align: start;
  padding-bottom: 1px;
  border-bottom: 1px solid #747474;
}
.permanent .explorer .main .hierarchy .current-folder-list .folder-list-el .el-capacity {
  flex-grow: 1;
  text-align: end;
  font-size: 75%;
  margin: auto;
  color: #747474;
}
.permanent .explorer .main .hierarchy .new-folder {
  display: flex;
  padding: 6px 15px;
  margin: 1px 0px 0px 0px;
  border-radius: 3px;
  font-size: 20px;
  background: #202020;
  box-sizing: border-box;
  transition: all 0.2s ease-out;
}
.permanent .explorer .main .hierarchy .new-folder:hover {
  background: #292929;
}
.permanent .explorer .main .hierarchy .new-folder .el-icon {
  flex-grow: 0;
  margin: auto 25px auto 0px;
  width: 25px;
  height: 25px;
}
.permanent .explorer .main .hierarchy .new-folder .el-name {
  flex-grow: 0;
  color: #747474;
  cursor: pointer;
  text-align: start;
}
.permanent .explorer .main .hierarchy .new-folder .el-capacity {
  flex-grow: 1;
  text-align: end;
  font-size: 75%;
  margin: auto;
  color: #747474;
}
.permanent .explorer .main .hierarchy .new-folder .el-input {
  flex-grow: 0;
  background: transparent;
  font-size: 20px;
  color: #747474;
  cursor: pointer;
  text-align: start;
  padding-bottom: 1px;
  border-bottom: 1px solid #747474;
}
html,
body {
  margin: 0px !important;
  padding: 0px !important;
  background-color: #181818;
  font-family: 'Diodrum', sans-serif;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  background-color: transparent;
  transition: all 2s ease-out;
}
::-webkit-scrollbar-thumb {
  width: 5px;
  background-color: #202020;
  border-radius: 5px;
}
::-webkit-scrollbar-thumb:hover {
  background-color: #202020;
}
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.no-select {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
button,
input,
textarea {
  border: none;
  font-family: 'Diodrum', sans-serif;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
}
a {
  text-decoration: none;
}
ul {
  padding: 0px;
}
ul li {
  list-style: none;
}
* {
  outline: none;
}
